.techHeader {
    width: 50%;
}

.developerHeader {
}

.row {
    padding: 12px 0;
    position: relative;
    display: flex;
    width: 606px;
    justify-content: start;
    font-size: 16px;
    line-height: 24px;
}
.row:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 606px;
    height: 1px;
    background: #E5E5E5;
}

.rowHeader {
    margin-top: 35px;
    padding: 5px 17px;
    display: flex;
    justify-content: start;
    width: 606px;
    font-size: 14px;
    line-height: 16px;
    background: #E5E5E5;
    font-weight: bold;

}

.loading {
}
.fone {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 1280px;
    height: 100%;
    z-index: 3;
    background-color: rgba(225, 225, 225, .8);
}
.loading img {
    position: absolute;
    top: 50%;
    left: 30%;
    width: 90px;
    height: 90px;
    z-index: 3;
}

.img {
    margin-left: 5px;
    width: 7px;
}